@charset "UTF-8";
@font-face {
  font-family: "Fira Sans";
  src: url("../fonts/Fira_Sans/FiraSans-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Fira Sans";
  src: url("../fonts/Fira_Sans/FiraSans-Medium.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat/Montserrat-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
  font-display: swap; }

*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-weight: normal;
  font-size: 15px;
  line-height: 24px;
  color: #484848;
  box-sizing: border-box;
  position: relative; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

@media (max-width: 320px) {
  body {
    width: 320px; } }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  padding-top: 45px;
  padding-bottom: 826px;
  position: relative;
  background-image: url("../../img/bg.png");
  background-repeat: no-repeat;
  background-position: center 0;
  box-sizing: border-box; }
  .page-header::before {
    content: "";
    width: 17px;
    height: 26px;
    position: absolute;
    left: 49.5%;
    top: 653px;
    background: url("../../img/mouse.svg") no-repeat;
    z-index: 5; }
  .page-header::after {
    content: "";
    width: 498px;
    height: 800px;
    position: absolute;
    top: -20px;
    left: 31.6%;
    background: url("../../img/girl.png") no-repeat;
    background-size: 498px auto; }

.page-header__logo {
  position: absolute;
  top: 32px;
  left: 44.8%; }

.page-header__nav {
  min-width: 1024px;
  padding-left: 110px;
  padding-right: 117px;
  box-sizing: border-box;
  margin: 0 auto;
  position: relative; }
  .page-header__nav::before {
    content: "";
    width: 0;
    height: 55px;
    position: absolute;
    top: 658px;
    left: 50%;
    border: 1px solid #fff;
    z-index: 5; }
  .page-header__nav::after {
    content: "";
    width: 1018px;
    height: 340px;
    top: 116px;
    left: 12.7%;
    position: absolute;
    background: url("../../img/Израиль.png") no-repeat;
    background-position: center 0; }

.page-header__list {
  padding: 0;
  margin: 0;
  margin-left: auto;
  display: flex;
  justify-content: space-between;
  list-style: none;
  color: #484848;
  position: relative; }
  .page-header__list::before {
    content: "";
    width: 52px;
    height: 52px;
    position: absolute;
    top: 594px;
    left: 48%;
    border-radius: 50%;
    border: 1px solid #fff;
    z-index: 5; }
  .page-header__list::after {
    content: "";
    width: 74px;
    height: 74px;
    position: absolute;
    top: 583px;
    left: 47%;
    border-radius: 50%;
    border: 1px dashed #fff;
    z-index: 5; }

.page-header__item--tel a {
  color: #484848; }

.page-header__item--call {
  border-bottom: 1px dashed #484848; }

@media (max-width: 768px) {
  .page-header {
    min-width: 320px;
    padding: 20px 0 575px;
    background-image: url("../../img/bgbg.png");
    background-position-y: 33px; }
    .page-header::before {
      width: 17px;
      height: 26px;
      left: 151px;
      top: 440px; }
    .page-header::after {
      width: 320px;
      height: 500px;
      left: 0;
      top: 33px;
      background-size: 320px auto; }
  .page-header__logo {
    width: 120px;
    height: 42px;
    top: 20px;
    left: 16px; }
  .page-header__nav {
    min-width: 320px;
    padding: 0;
    padding-right: 9px; }
    .page-header__nav::before {
      top: 471px; }
    .page-header__nav::after {
      width: 345px;
      height: 126px;
      top: 145px;
      left: -13px;
      background-size: 345px 126px;
      background-position: center 0; }
  .page-header__list {
    width: 130px;
    flex-direction: column; }
    .page-header__list::before {
      top: 405px;
      left: -38%; }
    .page-header__list::after {
      top: 395px;
      left: -46%; }
  .page-header__item {
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    text-align: right;
    text-align: right; }
    .page-header__item--tel {
      margin-bottom: 8px; }
    .page-header__item--call {
      width: 108px;
      margin-left: auto; } }

.site-title {
  width: 343px;
  margin: 0 auto;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: #fff;
  text-shadow: 0px 1px 4px rgba(0, 0, 0, 0.12);
  position: absolute;
  top: 539px;
  left: 37.5%;
  text-align: center; }

@media (max-width: 768px) {
  .site-title {
    width: 288px;
    left: 16px;
    top: 335px;
    font-size: 20px;
    line-height: 24px; } }
